:root {
  --primary-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
}

h1, h2, h3, h4 p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

.container {
  padding: 0 16px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 396px;
    margin-left: auto;
    margin-right: auto;
  }
}

.header-text {
  font-family: var(--secondary-font);
  text-align: center;
  text-transform: uppercase;
  color: #9acd32;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

